@use '../../globals/helpers' as *;

:host {
  display: block;
  width: 100%;
}

// Keyboard shortcut hint styling
.keyboard-shortcut {
  background-color: $color-surface-2;
  border-radius: 4px;
  border: 1px solid $color-border-1;

  bds-typo {
    color: $color-content-ghost;
  }
}

// Modal content wrapper
.search-modal-content {
  width: 90%;
  max-width: 640px;
  max-height: 70vh;
}

// Header styling
.search-modal-header {
  border-bottom: 1px solid $color-border-1;

  .search-icon {
    color: $color-content-ghost;
  }

  bds-input {
    flex: 1;
  }
}

// Results container
.search-modal-results {
  flex: 1;
  overflow-y: auto;
  max-height: 50vh;

  .search-result-item {
    cursor: pointer;
    transition: all 0.2s ease;
    border: 1px solid transparent;

    &:hover {
      bds-paper {
        background-color: $color-surface-1;
      }
    }

    &--selected {
      border-color: $color-primary;
    }

    .search-result-icon {
      width: 32px;
      height: 32px;
      border-radius: $border-radius;
      background-color: $color-surface-1;
      display: flex;
      align-items: center;
      justify-content: center;

      bds-icon {
        color: $color-content-default;
      }
    }

    .search-result-content {
      flex: 1;
      min-width: 0;

      .search-result-title {
        color: $color-content-default;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      .search-result-description {
        color: $color-content-ghost;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .search-result-hint {
      min-width: 80px;
      text-align: right;

      .keyboard-hint {
        color: $color-content-ghost;
      }
    }
  }
}

// Footer styling
.search-modal-footer {
  border-top: 1px solid $color-border-1;
  background-color: $color-surface-1;

  .hint {
    bds-typo {
      color: $color-content-ghost;
    }
  }
}

// Scrollbar styling
.search-modal-results::-webkit-scrollbar {
  width: 8px;
}

.search-modal-results::-webkit-scrollbar-track {
  background: transparent;
}

.search-modal-results::-webkit-scrollbar-thumb {
  background: $color-border-2;
  border-radius: 4px;
}

.search-modal-results::-webkit-scrollbar-thumb:hover {
  background: $color-border-3;
}

// Responsive adjustments
@media (max-width: 768px) {
  .search-modal-content {
    width: 95%;
    max-height: 80vh;
  }

  .search-result-hint {
    display: none;
  }
}
